import React from 'react';
import { useHistory } from 'react-router-dom';
import {Center, Box, Text, Divider} from "@chakra-ui/react";
import axios from 'axios';

import {apiURL,BatteryOption,BatteryOptionJson,Charger,ChargerJson,Units,Customers,Contracts,Departments} from "../../modules/interfaces"
import SearchBar from "../../components/SearchBar"
import AddCustomerButton from "../../components/buttons/AddButton";
import CompanyCard from "../../components/cards/CompanyCard";
import StatusBar from "../../components/StatusBar";
import Header from "../../components/Header";
import CustomerCard from 'components/components2/CustomerCard';

const ContractRegistrationHomePage = () => {
    //企業を取得
    const defaultCustomers:Customers[] = [];
    const [customers, setCustomers]: [Customers[], (customers: Customers[]) => void] = React.useState(defaultCustomers);

    React.useEffect(() => {
        axios
            .get<Customers[]>(apiURL+"api/v1/customers/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setCustomers(response.data);
            });
        }, []);
        console.log('customers data should be fetched at this point');
        console.log(customers)
    
    const defaultDepartments: Departments[] = [
        {
            department_id: 0,
            department_name: "",
            parent_id: 0,
            postal_code: "",
            address: "",
            daily_working_hour: { Int32: 0, Valid: false },
            weekly_holiday: { Int32: 0, Valid: false },
            name: "",
            position: "",
            mail: "",
            phone: "",
          },
        ];
    const [departments, setDepartments]: [
        Departments[],
        (departments: Departments[]) => void
    ] = React.useState(defaultDepartments);
        React.useEffect(() => {
        axios
          //.get<Departments[]>(apiURL +"api/v1/customerdepartment/?parent_id="+String(customer_id), {
          .get<Departments[]>(apiURL + "api/v1/departments/", {
            headers: {
              "Content-Type": "application/json",
            },
          })
          .then((response) => {
            setDepartments(response.data);
          });
      }, []);
      console.log("departments data should be fetched at this point");
      console.log(departments);
        
      let department_ids = [];
      if (
        customers != null &&
        customers.length > 0 &&
        departments != null &&
        departments.length > 0
      ) {
        for (let i = 0; i < customers.length; i++) {
          let tmp = [];
          for (let ii = 0; ii < departments.length; ii++) {
            if (customers[i].account_id == departments[ii].parent_id) {
              tmp.push(departments[ii].department_id);
            }
          }
          if (tmp.length == 0) {
            tmp = [-1];
          }
          department_ids.push(tmp); //[[1,2],[3],[...]]
        }
      }
        
      const defaultContracts: Contracts[] = [
        {
          contract_id: 0,
          contract_name: "",
          department_id: 0,
          contract_type: "",
          execution_date: 0,
          expiration_date: 0,
        },
      ];
      const [contracts, setContracts]: [
        Contracts[],
        (contracts: Contracts[]) => void
      ] = React.useState(defaultContracts);
      React.useEffect(() => {
        axios
          .get<Contracts[]>(apiURL + "api/v1/contracts/", {
            headers: {
              "Content-Type": "application/json",
            },
          })
          .then((response) => {
            setContracts(response.data);
          });
      }, []);
      console.log("contract data should be fetched at this point");
      console.log(contracts);
        
      let contract_num: number[] = [];
      if (customers != null && customers.length > 0 && contracts != null) {
        for (let i = 0; i < customers.length; i++) {
          let tmp = 0;
          if (department_ids[i] != null) {
            for (let ii = 0; ii < department_ids[i].length; ii++) {
              for (let iii = 0; iii < contracts.length; iii++) {
                if (department_ids[i][ii] == contracts[iii].department_id) {
                  tmp += 1;
                }
              }
            }
          }
          contract_num.push(tmp);
        }
      }

    const history = useHistory();

    return (
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center　padding="15px" flexDirection="column">
            <Box>
                <Text margin="15px" fontSize="22px" fontWeight="bold">新規契約登録先の選択/登録</Text>
                <Text margin="15px" fontSize="12px">
                    {`事業所・現場情報の登録を行う顧客を選択して下さい。\nもしくは以下のボタンより新しく顧客登録をしてください。`}
                </Text>
            </Box>
            <StatusBar activeStep={0}/>
            {/*<Divider/>*/}
            <Box margin="10px" marginTop="-10px">
                <AddCustomerButton
                    label="新規顧客登録"
                    onClick={()=>{
                    history.push(`/registration-view`)
                }}
            />
            </Box>
            {/*
            <Box marginBottom="10px">
                <SearchBar onChange={(value)=>{
                    // TODO:フィルタリングの処理を入れる
                    }}
                />
            </Box>
            */}
            <Box>
                {customers.map((value, index) => {
                    let account_id_str = String(value.account_id)
                    return (
                        <Box margin="20px">
                            <CustomerCard
                                customerName={value.corporation_name}
                                customerID={"RB_CUS_"+String(value.account_id)}
                                industry = {value.sector}
                                //TODO
                                //contract numをcustomer apiから取得できるように
                                contractNum={contract_num[index]}
                                //jobType=""
                                onClick={() => {
                                    history.push
                                    ({
                                        pathname: `/customer-department`,
                                        state: {
                                            account_id: value.account_id,
                                            company_name: value.corporation_name,
                                        },
                                    });
                                }}
                            />
                        </Box>
                    );
                })}
            </Box>
        </Center>
        </div>
    );
}

export default ContractRegistrationHomePage;