import React from "react";
import { Text, Icon, Center } from "@chakra-ui/react";
import { MdError, MdPlaylistAddCheck, MdBatteryAlert } from "react-icons/md";
import { RiBatterySaverFill } from "react-icons/ri";
import {CardType,NotificationCardProps} from "../../modules/interfaces"

{/*
type CardType =
  | "contract-request"
  | "battery-error"
  | "battery-warning"
  | "battery-request"
  | "action-error"
  | "action-warning";

interface NotificationCardProps {
  type: CardType;
  corporationName: string | undefined;
  departmentName: string | undefined;
  batteryOptionID: string | undefined;
  errorMessage: string | undefined;
  requiredAction: string | undefined;
  errorCode: string | undefined;
}
*/}

const NotificationCard = (props: NotificationCardProps) => {
  const {
    type,
    corporationName,
    departmentName,
    batteryOptionID,
    errorMessage,
    requiredAction,
    errorCode,
  } = props;

  const getCardData = () => {
    switch (type) {
      case "contract-uncomplite":
        return {
          color: "#333533",
          iconColor: "#62BB2C",
          icon: MdPlaylistAddCheck,
          text: `作成途中の契約があります。\n詳細を確認し、作成手続きを完了してください。`,
        };
      case "contract-norequest":
        return {
          color: "#333533",
          iconColor: "#62BB2C",
          icon: MdPlaylistAddCheck,
          text: `製造リクエストが未作成です。\nリクエスト作成を完了させて、工場での製造を開始できるようにしてください。`,
        };
      case "contract-nomanu":
        return {
          color: "#333533",
          iconColor: "#62BB2C",
          icon: MdPlaylistAddCheck,
          text: `製造情報が未作成です。\n製造リクエストに基づいた製造情報の作成を製造担当者へ指示してください。`,
        };
      case "action-error":
        return {
          color: "#333533",
          iconColor: "#EB5757",
          icon: MdError,
          text: `${errorMessage}が発生しています。\n「${requiredAction}」を実行してください。\nコード：${errorCode}`,
        };
      case "action-warning":
        return {
          color: "#333533",
          iconColor: "#FFEE32",
          icon: MdError,
          text: `${errorMessage}が発生しています。\n「${requiredAction}」を実行してください。\nコード：${errorCode}`,
        };
      case "battery-request":
        return {
          color: "#333533",
          iconColor: "#62BB2C",
          icon: RiBatterySaverFill,
          text: `${corporationName}、${departmentName} 向けに新しいバッテリーの製造が必要です。リクエストに基づいて製造を行ってください。`,
        };
      case "battery-error":
        if (batteryOptionID) {
          return {
            color: "#333533",
            iconColor: "#EB5757",
            icon: MdBatteryAlert,
            text: `${corporationName}、${departmentName} 内のバッテリーID：${batteryOptionID}で「${errorMessage}」が発生しています。詳細を確認してください。`,
          };
        } else {
          return {
            color: "#EB5757",
            iconColor: "#EB5757",
            icon: MdBatteryAlert,
            text: `エラーコード：${errorCode} が発生しています。\n「${requiredAction}」を実行してください。\nコード：${errorCode}`,
          };
        }
      case "battery-warning":
        if (batteryOptionID) {
          return {
            color: "#333533",
            iconColor: "#EFDB00",
            icon: MdBatteryAlert,
            text: `${corporationName}、${departmentName}内のバッテリーID：${batteryOptionID}で「${errorMessage}」が発生しています。詳細を確認してください。`,
          };
        } else {
          return {
            color: "#EFDB00",
            iconColor: "#EFDB00",
            icon: MdBatteryAlert,
            text: `${errorCode}が発生しています。\n「${requiredAction}」を実行してください。\nコード：${errorCode}`,
          };
        }
      default:
        return {
          color: "#333533",
          iconColor: "#333533",
          icon: MdError,
          text: "ERROR",
        };
    }
  };

  const cardData = getCardData();

  return (
    <Center
      w="375px"
      h="93px"
      flexDirection="row"
      color={cardData.iconColor}
      border="1px"
      borderColor={cardData.iconColor}
      padding="15px"
    >
      <Icon w="30px" h="30px" as={cardData.icon} />
      <Text fontSize="14px" color={cardData.color} marginLeft="10px">
        {cardData.text.split("\n").map((str, index) => (
          <React.Fragment key={index}>
            {str}
            <br />
          </React.Fragment>
        ))}
      </Text>
    </Center>
  );
};

export default NotificationCard;
